body {
  height: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: Inconsolata!important;
}

#root {
  min-height: 100%;
}

#sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  padding: 0!important;
  -webkit-box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
          box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
  color: rgba(255, 255, 255, 0.9);
  background: black;
}

#sidebar nav {
  height: 100%;
  float: top;
  
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  width: 100%;
  padding: 0.5rem 0 0.5rem 1.5rem;
}

li a{
  letter-spacing: 2px;
  font-size: 1rem;
  font-weight: 600;
  color: inherit!important;
  padding: 0.25rem 0.5rem;
  margin: 1rem 0;
}

li a i {
  padding-right: 1rem;
}

li a :hover {

  text-decoration: none;
}

li:hover { 
  color: rgba(255, 255, 255, 0.9);
  background-color: blueviolet;
  text-decoration: none;
}

nav .navbar {
  width: 100%;
  padding: 0!important;
}

.navbar-brand {
  font-size: 1.2rem;
  font-weight: 800;
  padding: 2rem 0 4rem 0;
}

.sidebar-sticky {
  width: inherit;
  

}

.sidebar-sticky div span {
  line-height: 2.5rem;
}

/* CONTENT */
#content {
  margin-right: 0;
  display: block;
  padding: 0 8rem!important;
  letter-spacing: 2px;
}

.jumbotron {
  border-radius: 0;
  margin-bottom: 0;
  padding-left: 2rem;
}

#rate-div {
  color: white;
  background-color: #FA724F;
}

#update-div {
  background-color: #ECE7DE;
}
.btn-group {
  border-radius: 1rem!important;
  padding-bottom: 1rem;
}

.btn {
  padding: 0rem, 1rem;
}

.btn :hover {
  background-color: rbga(48, 79, 254, 1);
}

.btn :focus {
  background-color: blue!important;
}

.btn:active {
  background-color: red!important;
}

#chart-header {
  padding: 1rem 0;
}

/* FOOTER */
footer a {
  padding: 1rem;
}


